.ui.text.container.main {
  margin: 0 auto !important;
}

.header {
  text-align: center
}

p, a {
  font-size: 1.5rem;
  color: black;
}

a:hover {
  color: royalblue;
}

.ui.text.container {
  max-width: 90% !important;
}

.article {
  min-width: 300px;
  padding: 20px 20px;
  margin-bottom: 10px;
  border: 2px solid black;
  border-radius: 5px; 
  transition: box-shadow 0.2s ease;
}
.article:hover {
  color: black;
  box-shadow: 8px 8px 7px 0px rgb(187, 187, 187);
}

.articleListVertical {
  display: flex;
  flex-direction: column;
}
.articleListHorizontal {
  display: flex;
  justify-content: left;
}
.articleListHorizontal > *:first-child {
  margin-right: 10px;
}

.articleInfo {
  width: 100%;
  display: flex !important;
  justify-content: space-between;
  align-items: center;
}

.articleSourceLink {
  font-size: 1.2rem;
}

.comments {
  max-width: 100% !important;
}


@media only screen and (max-width: 425px) {
  .articleListHorizontal {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .articleListHorizontal > *:first-child {
    margin-right: 0;
  }
  .article {
    margin-bottom: 10px;
  }
}